<!-- eslint-disable vuejs-accessibility/label-has-for -->
<template>
  <div class="modal" tabindex="-1" ref="AddEdit">
    <div class="modal-dialog modal-lg modal-dialog-centered">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title">Modal title</h5>
          <button
            type="button"
            class="btn-close"
            data-bs-dismiss="modal"
            aria-label="Close"
          ></button>
        </div>
        <div class="modal-body">
          <div class="row g-5">
            <div class="col-9 border-end">
              <!-- 題目 -->
              <div class="input-group">
                <span class="input-group-text">題目</span>
                <textarea
                  class="form-control"
                  aria-label="With textarea"
                  v-model="tempData.title"
                ></textarea>
              </div>
              <hr />
              <!-- 單選題 -->
              <form class="row row-cols-1 g-0" v-if="tempData.type === '單選'">
                <template v-for="(item, index) in tempData.select" :key="index">
                  <div class="col mb-4">
                    <div class="input-group input-group-lg">
                      <div class="input-group-text">
                        <input
                          class="form-check-input mt-0"
                          type="radio"
                          name="multipleChoice"
                          value="true"
                          aria-label="Radio button for following text input"
                          v-model="item.isAnswer"
                          @click="changeAnswer(index)"
                        />
                      </div>
                      <input
                        type="text"
                        class="form-control"
                        aria-label="Text input with radio button"
                        v-model="item.answer"
                      />
                    </div>
                  </div>
                </template>
              </form>
              <!-- 複選題 -->
              <form
                class="row row-cols-1 g-0"
                v-else-if="tempData.type === '複選'"
              >
                <template v-for="(item, index) in tempData.select" :key="index">
                  <div class="col mb-4">
                    <div class="input-group input-group-lg">
                      <div class="input-group-text">
                        <input
                          class="form-check-input mt-0"
                          type="checkbox"
                          name="multipleChoiceQuestions"
                          aria-label="Checkbox for following text input"
                          v-model="item.isAnswer"
                        />
                      </div>
                      <input
                        type="text"
                        class="form-control"
                        aria-label="Text input with checkbox"
                        v-model="item.answer"
                      />
                    </div>
                  </div>
                </template>
              </form>
              <!-- 問答 -->
              <form class="row g-3" v-else>
                <div class="input-group">
                  <textarea
                    class="form-control"
                    aria-label="With textarea"
                  ></textarea>
                </div>
              </form>
            </div>
            <!-- right -->
            <div class="col-3">
              <form class="row g-4">
                <!-- 題型 -->
                <div class="col-12">
                  <label for="inputState" class="form-label"
                    ><i class="bi bi-chat-left-dots me-3"></i>題型</label
                  >
                  <select
                    id="inputState"
                    class="form-select form-select-lg"
                    v-model="tempData.type"
                  >
                    <option value="單選">單選</option>
                    <option value="複選">複選</option>
                    <option value="問答">問答</option>
                  </select>
                </div>
                <hr />
                <!-- 時間 -->
                <div class="col-12">
                  <label for="inputState" class="form-label"
                    ><i class="bi bi-alarm me-3"></i>時間限制</label
                  >
                  <select
                    id="inputState"
                    class="form-select form-select-lg"
                    v-model="tempData.times"
                  >
                    <option value="5">5秒</option>
                    <option value="10">10秒</option>
                    <option value="20">20秒</option>
                    <option value="30">30秒</option>
                    <option value="60">1分鐘</option>
                    <option value="90">1分鐘30秒</option>
                    <option value="120">2分鐘</option>
                    <option value="180">3分鐘</option>
                    <option value="240">4分鐘</option>
                  </select>
                </div>
                <!-- 分數 -->
                <div class="col-12">
                  <label for="inputState" class="form-label"
                    ><i class="bi bi-star-fill me-3"></i>分數</label
                  >
                  <select
                    id="inputState"
                    class="form-select form-select-lg"
                    v-model="tempData.score"
                  >
                    <option value="normal">標準</option>
                    <option value="double">兩倍分數</option>
                    <option value="none">無分數</option>
                  </select>
                </div>
              </form>
            </div>
          </div>
        </div>
        <div class="modal-footer">
          <button
            type="button"
            class="btn btn-secondary"
            data-bs-dismiss="modal"
          >
            Close
          </button>
          <button type="button" class="btn btn-primary">Save changes</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { Modal } from 'bootstrap';

export default {
  data() {
    return {
      modal: '',
      tempData: {},
    };
  },
  methods: {
    showModal(
      data = {
        type: '單選',
        times: 30,
        score: 'normal',
        select: [
          { answer: '', isAnswer: true },
          { answer: '' },
          { answer: '' },
          { answer: '' },
        ],
      },
    ) {
      this.tempData = JSON.parse(JSON.stringify(data));
      this.modal.show();
    },
    changeAnswer(i) {
      this.tempData.select.forEach((item) => {
        // eslint-disable-next-line no-param-reassign
        item.isAnswer = false;
      });
      this.tempData.select[i].isAnswer = true;
    },
  },
  mounted() {
    this.modal = new Modal(this.$refs.AddEdit);
  },
};
</script>
