<!-- eslint-disable vuejs-accessibility/anchor-has-content -->
<!-- eslint-disable max-len -->
<template>
  <section class="px-3">
    <div class="row g-4">
      <div class="col-3">
        <div class="list-group">
          <template v-for="(item, index) in folderList" :key="index">
            <a
              href="#"
              class="list-group-item list-group-item-action"
              :class="{ active: tempData.folderName === item.folderName }"
              aria-current="true"
              @click.prevent="selectClass(item.folderName)"
            >
              {{ item.folderName }}
            </a>
          </template>
          <button
            class="btn btn-heightlight w-100 d-flex justify-content-between text-light"
            @click="this.$refs.addClass.showModal()"
          >
            <span>建立題庫資料夾</span><i class="bi bi-plus-lg"></i>
          </button>
        </div>
      </div>
      <div class="col-9">
        <div class="card">
          <div class="card-header bg-primary">
            <ul class="nav nav-tabs card-header-tabs">
              <li class="nav-item">
                <button
                  class="nav-link"
                  :class="[this.route === '題目' ? 'active' : 'text-light']"
                  @click="this.route = '題目'"
                >
                  <i class="bi bi-book"></i>
                  題目({{ topicLength }})
                </button>
              </li>
              <li class="nav-item">
                <button
                  class="nav-link"
                  :class="[this.route === '顯示答案' ? 'active' : 'text-light']"
                  @click="this.route = '顯示答案'"
                >
                  <i class="bi bi-eyeglasses"></i>
                  顯示答案
                </button>
              </li>
              <li class="nav-item ms-auto">
                <button
                  class="nav-link"
                  :class="[this.route === '設定' ? 'active' : 'text-light']"
                  @click="this.route = '設定'"
                >
                  <i class="bi bi-gear-fill"></i>
                  設定
                </button>
              </li>
            </ul>
          </div>
          <div
            class="card-body overflow-y-scroll"
            style="height: 65vh"
            ref="items"
          >
            <template
              v-for="(item, index) in tempData.topicList"
              :key="item.id"
            >
              <div
                class="card mb-4 shadow animate__animated animate__infinite headShake text-start"
                :class="{ btn: route === '題目' }"
                :id="item.id"
              >
                <div class="row g-0">
                  <div :class="[route !== '設定' ? 'col-12' : 'col-10']">
                    <div class="card-body">
                      <div class="text-black-50">
                        {{ index + 1 }}-{{ item.type }}
                        <i
                          class="bi bi-check-all text-primary"
                          v-if="item.type === '複選'"
                        ></i
                        ><i
                          class="bi bi-ui-radios-grid"
                          style="color: #a46039"
                          v-else-if="item.type === '單選'"
                        ></i>
                        <i
                          class="bi bi-chat-text text-heightlight"
                          style="color: #a46039"
                          v-else
                        ></i>
                      </div>
                      {{ item.title }}
                    </div>
                    <div class="card-footer" v-if="route === '顯示答案'">
                      <template v-for="(data, key) in item.select" :key="key">
                        <div
                          class="d-flex justify-content-between border-bottom"
                        >
                          <div :class="[data.isAnswer ? 'text-primary': '']">{{ data.select }}.{{ data.answer }}</div>
                          <div>
                            <i
                              class="bi bi-check2 text-success h4"
                              v-if="data.isAnswer"
                            ></i>
                            <i class="bi bi-x-lg text-danger h4" v-else></i>
                          </div>
                        </div>
                      </template>
                    </div>
                  </div>
                  <div class="col-1" v-if="route === '設定'">
                    <a
                      href="#"
                      class="h-100 bg-primary d-flex justify-content-center align-items-center"
                      @click.prevent="this.$refs.addEdit.showModal(item)"
                    >
                      <i class="bi bi-pencil text-light h5"></i>
                    </a>
                  </div>
                  <div class="col-1" v-if="route === '設定'">
                    <a
                      href="#"
                      class="h-100 bg-heightlight d-flex justify-content-center align-items-center rounded-end"
                      @click.prevent="this.$refs.delete.deleteTopic(item)"
                    >
                      <i class="bi bi-x-lg text-light h5"></i>
                    </a>
                  </div>
                </div>
              </div>
            </template>
          </div>
          <div class="card-footer text-center">
            <button
              class="btn btn-outline-primary"
              @click="this.$refs.addEdit.showModal()"
            >
              新增題目
            </button>
          </div>
        </div>
      </div>
    </div>
  </section>
  <AddClassModal ref="addClass" />
  <AddEditModal ref="addEdit" />
  <DeleteModal ref="delete" />
</template>

<script>
import AddClassModal from '@/components/group/AddClassModal.vue';
import AddEditModal from '@/components/topic/AddEditModal.vue';
import DeleteModal from '@/components/topic/DeleteModal.vue';
import Sortable from 'sortablejs';
import choseData from '@/assets/jsons/chose.json';
import folderData from '@/assets/jsons/topic.json';

export default {
  data() {
    return {
      route: '題目',
      topicLength: 0,
      sortable: '',
      folderList: folderData,
      choseList: choseData,
      tempData: {},
      newList: '',
    };
  },
  components: {
    AddClassModal,
    AddEditModal,
    DeleteModal,
  },
  methods: {
    selectClass(i) {
      this.folderList.forEach((item) => {
        if (item.folderName === i) {
          this.tempData = item;
          this.topicLength = item.topicList.length;
        }
      });
    },
  },
  mounted() {
    this.sortable = Sortable.create(this.$refs.items, {
      animation: 500,
      onStart() {
        const headShakeList = Object.values(
          // eslint-disable-next-line comma-dangle
          document.getElementsByClassName('headShake')
        );
        headShakeList.forEach((item) => {
          item.classList.add('animate__headShake');
        });
      },
      onEnd() {
        const headShakeList = Object.values(
          // eslint-disable-next-line comma-dangle
          document.getElementsByClassName('headShake')
        );
        headShakeList.forEach((item) => {
          item.classList.remove('animate__headShake');
        });
        const tempList = document.getElementsByClassName('headShake');
        this.newList = [];
        Object.values(tempList).forEach((item) => {
          this.newList.push(item.id);
        });
      },
    });
    // eslint-disable-next-line prefer-destructuring
    this.tempData = this.folderList[0];
    this.topicLength = this.folderList[0].topicList.length;
  },
};
</script>
